<template>
  <div class="hello">
    <el-input
        id="ipt"
        v-model="searchData"
        ref="inputVal"
        @blur="blurFun"
        @focus="focusFun"
        placeholder="请输入搜索内容"
        icon="el-icon-search"
    >
    </el-input>
    <el-button
        type="primary"
        @click="searchTo"
        icon="el-icon-s-promotion"
    >
      点击搜索</el-button>

  </div>

</template>

<script>

export default {
  name: 'HelloWorld',
  data(){
    return{
      searchData:"", // 搜索信息
      arr:[], // 存储关键词
      listIndex:-1, // 设置初始索引
      focusV: false,
      tip: "请输入搜索内容"
    }
  },
  methods:{
    focusFun(){
      this.onfocus();
    },
    blurFun(){
      this.focusV = false;
      // console.log("失去焦点")
    },
    searchTo(){
      window.open("https://www.baidu.com/s?wd="+this.searchData);
      this.searchData = ""
      // alert(this.searchData)
    },
    onfocus(){
      // var ipt = document.getElementById("ipt");
      // ipt.focus();
      this.$refs.inputVal.focus();
      this.focusV = true;
      // console.log("获取焦点")
    },
    //按键恢复
    keyDownReview () {
      document.onkeydown = function (event) {
        var e = event || window.event;
        e.returnValue = true;
      }
    },
    //按钮监听键盘
    keyDown () {
      let that = this;
      //监听键盘按钮
      document.onkeydown = function (event) {
        var e = event || window.event;
        var keyCode = e.keyCode || e.which;
        // console.log(e.keyCode, e.which)
        if (keyCode === 13){
          // console.log("按下Enter");
          // console.log(that.focusV);
           if (that.focusV === true) {
             that.searchTo();
           }
        }
        // if (e && e.preventDefault) {
        //   e.preventDefault();
        // } else {
        //   window.event.returnValue = false;
        // }
      }
    },
  },
  beforeDestroy () {
    this.keyDownReview()
  },
  mounted() {
    this.onfocus();
    this.keyDown();
  },
  props: {
    msg: String
  }


}
</script>

