<template>
  <el-button
      icon="el-icon-view"
      v-model="value"
      @click="calculateV"
  >
  </el-button>
</template>

<script>
export default {
  name: "IconPic",
  data(){
    return{
      value: 1,
    }
  },
  props:{
    total: {
      type: Number,
      default: 1
    }
  },
  methods:{
    calculateV(){
      console.log()
      this.$emit("sendV", (this.value++) % (this.total));
      // console.log(this.value, this.total)
    }
  },
  mounted() {
    // console.log(this.value, this.total)
  }
}
</script>

